.start-quiz {
    width: 193px;
    height: 52px;
    background-color: #4d5b9e;
    color: #f5f7fb;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 500;
    border: none;
    border-radius: 15px;
    cursor: pointer;
}

.check-answers {
    width: 120px;
    height: 35px;
    background-color: #4d5b9e;
    color: #f5f7fb;
    font-family: "Inter", sans-serif;
    font-size: 10.24px;
    font-weight: 600;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin: 20px 0;
}

.play-again {
    width: 104px;
    height: 30px;
    background-color: #4d5b9e;
    color: #f5f7fb;
    font-family: "Inter", sans-serif;
    font-size: 10.24px;
    font-weight: 600;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}
